.cardContainer {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  width: 150px;
  height: fit-content;
  margin: 0 10px;
  position: relative;
  box-shadow: 0 0 10px black;
  border-radius: 5px;
}

.cardHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  padding-top: 5px;
  background: #4a9ffb;
  border-radius: 0 0 5px 5px;
  overflow: hidden;
}

.cardHeader .title {
  margin: 0 10px;
}

.cardHeader .voteAverage {
  text-align: center;
  height: 20px;
  padding: 5px;
  margin-right: 10px;
  background: #78b9ff;
  font-weight: 400;
  text-shadow: 2px 1px 2px #565656;
}

.image {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.overview {
  position: absolute;
  text-align: center;
  padding: 0 3px;
  bottom: 74px;
  background: #0000007d;
  color: #fff;
  font-size: smaller;
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

.cardContainer:hover .overview {
  opacity: 1;
}

.overview a {
  padding-left: 5px;
  color: #4a9ffb;
}

.overview a:hover {
  text-decoration: underline;
}

#green {
  color: rgb(0, 255, 0);
}
#orange {
  color: orange;
}
#red {
  color: red;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .cardContainer {
    width: 250px;
    height: 440px;
  }
  .overview {
    bottom: 65px;
    font-size: smaller;
  }
}
